<!-- eslint-disable -->
<template>
  <div id="equipa" class="equipa-wrapper">
    <div class="equipa">
      <div class="left lateral">
        <div class="person-wrapper">
          <div class="team">
            <div
              class="person"
              :class="{'active': isActive}"
              v-for="p in people"
              :key="p.id"
              :data-person-id="p.id"
            >
              <div class="pic" :style="{ 'background-image': 'url(' + p.pic + ')' }"></div>
              <div class="name">{{ p.name }}</div>
              <div class="description">
                <i v-html="p.text"></i>
              </div>
            </div>
          </div>
          <div class="indicators">
            <div
              class="idc"
              v-for="p in people"
              :key="p.id"
              :class="{'active': isActive}"
              @click="currentSlide(p.id)"
            ></div>
          </div>
        </div>
      </div>
      <div class="right lateralR">
        <!-- <div class="subtext">estamos aqui para si</div> -->
        <h2 class="title main">A nossa equipa</h2>
        <div class="line"></div>
        <div class="txt">
          <p
            class="p"
            :class="{'active': isActive}"
            v-for="p in people"
            :key="p.id"
            v-html="p.content"
          ></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import quote from "../../assets/quote.png";
import axios from "axios";
import check from "../../assets/check.png";

export default {
  data() {
    return {
      check,
      quote,
      isActive: true,
      index: 1,
      slides: 0,
      people: [
        {
          id: 0,
          name: "",
          text: "",
          content: "",
          quote: "",
          pic: "",
        },
      ],
    };
  },
  methods: {
    clickIndicator(n, dir) {
      var i;
      var slides = document.querySelectorAll(".team .person");
      var dots = document.querySelectorAll(".indicators .idc");
      var text = document.querySelectorAll(".right .txt .p");
      // var quote = document.querySelectorAll(".quote .txt");
      if (n > slides.length) {
        this.index = 1;
      }
      if (n < 1) {
        this.index = slides.length;
      }
      for (i = 0; i < slides.length; i++) {
        slides[i].className = "person";
        text[i].className = "p";
        // quote[i].className = "txt";
      }
      for (i = 0; i < dots.length; i++) {
        dots[i].className = dots[i].className.replace(" active", "");
        this.isActive = false;
      }
      slides[this.index - 1].className = "person active";
      dots[this.index - 1].className += " active";
      text[this.index - 1].className += " active";
      // text[this.index - 1].style.transform =
      //   "translateX(calc(50% * " + dir + "))";
      // quote[this.index - 1].className += " active";
      // console.log(dir);
      this.isActive = true;
    },

    prevSlides(n) {
      this.clickIndicator((this.index -= n), 1);
    },
    plusSlides(n) {
      this.clickIndicator((this.index += n), -1);
    },
    currentSlide(n) {
      this.clickIndicator((this.index = n), -1);
    },
    FadeIn() {
      var allMods = $(".movement, .cover");
      var delayedMods = $(".movement-delayed");
      var lateralMods = $(".lateral, .lateralR");
      var svg = $("#svg");

      (function ($) {
        $.fn.visible = function (partial, adjust) {
          if (adjust === undefined) {
            adjust = 0;
          }
          var elem = $(this),
            wind = $(window),
            viewTop = wind.scrollTop(),
            viewBottom = viewTop + wind.height(),
            elem_top = elem.offset().top + adjust,
            elem_bottom = elem_top + elem.height() - adjust,
            compareTop = partial === true ? elem_bottom : elem_top,
            compareBottom = partial === true ? elem_top : elem_bottom;

          return compareBottom <= viewBottom && compareTop >= viewTop;
        };
      })(jQuery);

      var win = $(window);

      var xmlTeste = true;

      allMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          el.addClass("placed");
        }
      });
      svg.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          xmlTeste &&
            el.append(
              "<img src='assets/img/elemento_animated.svg' alt=''></img>"
            );
          xmlTeste = false;
        }
      });
      lateralMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 100)) {
          el.addClass("placed");
        }
      });

      delayedMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 800)) {
          el.addClass("placed");
        }
      });

      win.scroll(function (event) {
        allMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            el.addClass("placed");
          }
        });
        lateralMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 100)) {
            el.addClass("placed");
          }
        });
        delayedMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 800)) {
            el.addClass("placed");
          }
        });
        svg.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            xmlTeste &&
              el.append(
                "<img src='assets/img/elemento_animated.svg' alt=''></img>"
              );
            xmlTeste = false;
          }
        });
      });
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    var slider = document.querySelector(".team");
    var slides = document.querySelectorAll(".team .person");

    if (window.innerWidth < 600) {
      slider.addEventListener("touchstart", handleTouchStart, false);
      slider.addEventListener("touchmove", handleTouchMove, false);

      var xDown = null;
      var yDown = null;

      function getTouches(evt) {
        return evt.touches || evt.originalEvent.touches;
      }

      function handleTouchStart(evt) {
        const firstTouch = getTouches(evt)[0];
        xDown = firstTouch.clientX;
        yDown = firstTouch.clientY;
      }

      function handleTouchMove(evt) {
        if (!xDown || !yDown) {
          return;
        }

        var xUp = evt.touches[0].clientX;
        var yUp = evt.touches[0].clientY;

        var xDiff = xDown - xUp;
        var yDiff = yDown - yUp;

        var active = document.querySelector(".team .person.active");
        var id = active.getAttribute("data-person-id");

        if (Math.abs(xDiff) > Math.abs(yDiff)) {
          /*most significant*/
          if (xDiff > 0) {
            /* left swipe */
            vm.plusSlides(1);
          } else {
            /* right swipe */
            vm.prevSlides(1);
          }
        } // else {
        // if (yDiff > 0) {
        //   /* up swipe */
        // } else {
        //   /* down swipe */
        // }
        // }
        /* reset values */
        xDown = null;
        yDown = null;
      }
    }

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getAbout.php")
      .then((resp) => {
        const data = resp.data;
        var result = data.split("||");
        var equipa = JSON.parse(result[3]);
        var equipaArray = [];

        map.call(equipa, function (e) {
          var sl = vm.$data.slides++;

          if (e.img_src == null) {
            equipaArray.push({
              id: sl + 1,
              name: e.name,
              text: e.cargo,
              content: e.description,
              quote: e.quote,
              pic: check,
            });
          } else {
            var src = e.img_src.split("uploads");
            equipaArray.push({
              id: sl + 1,
              name: e.name,
              text: e.cargo,
              content: e.description,
              quote: e.quote,
              pic: "https://beta.anacarolinapereira.pt/uploads" + src[1],
            });
          }
        });

        vm.$data.people = equipaArray;
      })
      .then(() => {
        vm.FadeIn();
        vm.clickIndicator(1);

        var location = window.location.href;
        var split_url = location.split("/");
        var ref = split_url[split_url.length - 1];

        if (ref.includes("#")) {
          var hash = ref.split("#");
          var top = $("#" + hash[1])[0].offsetTop;

          setTimeout(() => {
            $("html, body").animate({ scrollTop: top - 100 }, 1000);
          }, 500);
        }

        var txt = document.querySelector(".right > .txt");
        var pgs = document.querySelectorAll(".right .txt > p");

        window.addEventListener("resize", function () {
          if (window.innerWidth < 992) {
            var heights = [];

            Array.prototype.max = function () {
              return Math.max.apply(null, this);
            };

            Array.prototype.min = function () {
              return Math.min.apply(null, this);
            };

            map.call(pgs, function (pg) {
              heights.push(pg.clientHeight);
            });
            txt.style.minHeight = heights.min() + "px";
            txt.style.height = heights.max() + "px";

            // window.addEventListener("resize", function () {
            //   map.call(pgs, function (pg) {
            //     heights.push(pg.clientHeight);
            //   });

            //   txt.style.minHeight = heights.min() + "px";
            //   txt.style.height = heights.max() + "px";
            // });
          } else {
            txt.style.minHeight = "13.75rem";
            txt.style.height = "unset";
          }
        });
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquoise: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.equipa-wrapper {
  background-color: #fafafa;
  height: pxToEm(655);
  padding: $pd-desktop;
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
    height: auto;
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  .equipa {
    margin: auto;
    max-width: pxToEm(1400);
    width: 100%;
    display: flex;
    justify-content: space-between;

    @media only screen and (max-width: 991px) {
      flex-direction: column;
      align-items: center;
    }

    @media only screen and (max-width: 599px) {
      padding-bottom: pxToEm(50);
    }

    .left {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      @media only screen and (max-width: 991px) {
        margin-bottom: pxToEm(20);
        width: 100%;
      }

      .person-wrapper {
        width: pxToEm(240);
        height: pxToEm(345);
        background-color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: pxToEm(60);
        position: relative;
        overflow: hidden;

        @media only screen and (max-width: 991px) {
          width: 100%;
          padding: 3.75em 0;
          margin-top: pxToEm(50);
          height: 100%;
        }

        .team {
          display: flex;
          justify-content: space-between;
          position: relative;
          width: 100%;
          height: 100%;

          .person {
            display: flex;
            flex-direction: column;
            align-items: center;
            position: absolute;
            width: 100%;
            opacity: 0;
            transition: 0.5s;

            &.active {
              opacity: 1;
            }

            .pic {
              border-radius: $circle;
              width: pxToEm(190);
              height: pxToEm(190);
              background-color: #cbcbcb;
              background-size: cover;
            }

            .name {
              font-size: pxToEm(22);
              color: $turquoise;
              margin-top: pxToEm(30);
            }

            .description {
              font-family: Georgia, "Times New Roman", Times, serif;
              font-size: pxToEm(18);
              color: #828282;
              text-align: center;
              margin-top: pxToEm(30);
            }
          }
        }

        .indicators {
          display: flex;
          justify-content: space-around;
          align-items: center;
          width: 25%;
          margin-top: pxToEm(320);

          @media screen and (max-width: 991px) {
            width: 10%;
          }

          .idc {
            width: pxToEm(10);
            height: pxToEm(10);
            background-color: #bce0fd;
            border-radius: $circle;
            cursor: pointer;

            &.active {
              background-color: $turquoise;
            }
          }
        }
      }
    }

    .right {
      max-width: pxToEm(900);
      width: 100%;
      height: pxToEm(440);
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;

      @media only screen and (max-width: 991px) {
        align-items: center;
        text-align: center;
        height: 50%;
        width: 100%;
        max-width: 100%;
      }

      .subtext {
        font-size: pxToEm(20);
        text-transform: uppercase;
        color: #828282;
      }

      .title {
        margin: 0;
        font-weight: normal;
        // margin-top: pxToEm(20);
        color: #828282;
      }

      .line {
        width: pxToEm(80);
        height: pxToEm(1);
        background-color: $turquoise;
        margin-top: pxToEm(10);
      }

      > .txt {
        font-family: "OS-Regular";
        font-size: pxToEm(18);
        color: #828282;
        margin: pxToEm(20) 0;
        min-height: pxToEm(220);

        @media only screen and (max-width: 991px) {
          width: 100%;
          /* max-width: 100%; */
          margin-top: 0.625rem;
          height: auto;
          display: flex;
          // height: pxToEm(400);
          // height: auto;
        }

        // @media only screen and (max-width: 767px) {
        //   height: pxToEm(400);
        // }

        // @media only screen and (max-width: 580px) {
        //   height: pxToEm(500);
        // }

        // @media only screen and (max-width: 440px) {
        //   height: pxToEm(600);
        // }

        // @media only screen and (max-width: 370px) {
        //   height: pxToEm(800);
        // }

        // @media only screen and (max-width: 330px) {
        //   height: pxToEm(950);
        // }

        .p {
          position: absolute;
          opacity: 0;
          transition: 0.5s;
          text-align: justify;

          @media only screen and (max-width: 991px) {
            height: auto;
            margin: 0;
            width: 100%;
            position: absolute;
          }

          @media only screen and (max-width: 991px) {
            // &:first-child {
            //   transform: translateX(50%);
            // }

            // &:last-child {
            //   transform: translateX(-50%);
            // }
          }

          &.active {
            opacity: 1;
          }
        }
      }

      .quote {
        margin-top: pxToEm(20);

        @media only screen and (max-width: 991px) {
          width: 100%;
          max-width: 100%;
          min-height: pxToEm(100);
          margin-bottom: pxToEm(20);
        }

        .icon {
          width: pxToEm(16);
          height: pxToEm(15);
          background-size: cover;
        }

        > .txt {
          font-family: Georgia, "Times New Roman", Times, serif;
          font-size: pxToEm(20);
          color: #828282;
          margin-top: pxToEm(15);
          opacity: 0;
          transition: 0.5s;
          position: absolute;

          &.active {
            opacity: 1;
          }
        }
      }
    }
  }
}
</style>



